import { useState, useEffect } from 'react'
import { getAllUserRents } from '../../services/rents'
import moment from 'moment'
import { Typography, Divider, Table, Space, Skeleton, Button, Tag, Popconfirm, Tabs } from 'antd'
import { Gradient } from 'react-gradient'

const { TabPane } = Tabs;

const Rents = () => {
  const [rents, setRents] = useState(null)
  const [tab, setTab] = useState('renter')

  async function getRents(role) {
    try {
      setRents(null)
      const {data} = await getAllUserRents(role)
      console.log(data.rents);
      setRents(data.rents)
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    console.log(tab)
    getRents(tab)
  }, [tab])

  function callback(key) {
    console.log(key)
    if (key == 1) {
      setTab('renter')
    } else {
      setTab('owner')
    }
  }

  const renderDate = (value, row, index) => {
    if (row.type === 'day') {
      return moment(value).format('YYYY/MM/DD')
    } else {
      return moment(value).format('YYYY/MM/DD HH:00')
    }
  }

  const renderTotal = (value, row, index) => {
    let text = `$${value} for `
    if (row.type === 'day') {
      return text + moment(row.rentedTo).diff(moment(row.rentedFrom), 'days') + 1
    } else {
      return text + moment(row.rentedTo).diff(moment(row.rentedFrom), 'hours') + 1
    }
  }

  const columns = [
    {
      title: 'Product',
      dataIndex: 'product',
      key: 'product',
      render: product => <a>{product.name}</a>,
    },
    {
      title: 'Rented from',
      dataIndex: 'rentedFrom',
      key: 'rentedFrom',
      //render: rentedFrom => moment(rentedFrom).format('YYYY/MM/DD'),
      render: renderDate,
    },
    {
      title: 'Rented to',
      dataIndex: 'rentedTo',
      key: 'rentedTo',
      render: renderDate,
    },
    {
      title: 'Total',
      dataIndex: 'total',
      key: 'total',
      render: renderTotal,
    },
    {
      title: tab === 'owner' ? 'Renter' : 'Owner',
      dataIndex: tab,
      key: tab,
      render: user => tab === 'owner' ? user.firstName : (user.storeName ? user.storeName : user.firstName),
    },
  ];

  return (
    <>
      <div className='user-info-header'>
        <Space size="middle">
          <Gradient
            gradients={[
              ['#00a1ba', '#9cd873'],
            ]}
            property="text"
            element="h1"
            angle="45deg"
            className="text ant-typography"
          >
            Rents
          </Gradient>
        </Space>
      </div>
      <Divider />
      <Tabs onChange={callback} type="card">
        <TabPane tab="Rented by me" key="1">
          {rents ? <Table dataSource={rents} columns={columns} /> : <Skeleton active />}
        </TabPane>
        <TabPane tab="Owned by me" key="2">
          {rents ? <Table dataSource={rents} columns={columns} /> : <Skeleton active />}
        </TabPane>
      </Tabs>
    </>
  )
}

export default Rents
